@import "../../variables.scss";

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;

  label {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 16px;

    p {
      font-size: 20px;
      text-align: center;
      color: $primary-color;
      margin: 0 16px;

      &.errorText {
        color: $secondary-color;
      }
    }
  }

  input {
    margin: 24px;
    border: 1px solid $primary-color;
    border-radius: 20px;
    padding: 16px;
    height: 32px;
    width: 250px;
    font-size: 32px;
    color: $primary-color;
    transition: transform 200ms ease-in;

    &:focus {
      outline: none;
      border-width: 2px;
      transform: scale(1.04);
    }

    &.invalid {
      border-color: $secondary-color;
    }
  }

  button {
    border: none;
    border-radius: 16px;
    box-shadow: 3px 3px 3px #888;
    padding: 8px;
    height: 64px;
    width: 180px;
    background: linear-gradient(
      45deg,
      rgba($secondary-color, 1) 0%,
      rgba(241, 189, 157, 1) 45%
    );
    font-family: Philosopher;
    font-size: 24px;
    text-transform: uppercase;
    transition: transform 200ms ease-in;

    &.valid:hover {
      transform: scale(1.03);
      box-shadow: 6px 6px 6px #999;
    }

    &.valid:active {
      box-shadow: 2px 2px 2px #777;
    }
  }
}
