.logo {
  height: 280px;
  margin-top: 12px;

  &-image {
    display: block;
    height: 100%;
    margin: 0 auto;
  }

  &.loading {
    animation: grow-and-spin 3s linear;
  }
}

@keyframes grow-and-spin {
  0% {
    transform: translateY(0) scale(1) rotateY(0);
  }
  25% {
    transform: translateY(50%) scale(1.5) rotateY(0deg);
  }
  50% {
    transform: translateY(50%) scale(1.5) rotateY(180deg);
  }
  75% {
    transform: translateY(50%) scale(1.5) rotateY(0deg);
  }
  100% {
    transform: translateY(0) scale(1) rotateY(0);
  }
}
