@import '../variables.scss';

.menu-choice {
    width: 100%;
    text-align: center;

    input[type="radio"] {
      display: none;
      &:checked {
        + .box {
          background-color: $green;
        }
      }
    }
    .box {
      width: 92px;
      height: 48px;
      background-color: $green;
      display: inline-block;
      text-align: center;
      position: relative;
      font-size: 12px;
      padding: 6px;
      border-radius: 20px;

      &.menu-option {
        background-color: grey;
        transition: all 250ms ease;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        
        &:active {
          transform: translateY(10px);
        }
      }

      &:not(last-child) {
        margin-right: 4px;
      }

      &.kids {
        background-color: $green;
        width: 184px;
        margin-top: 16px;
      }

      span {
        position: absolute;
        transform: translate(0, 67%);
        left: 0;
        right: 0;
        transition: all 300ms ease;
        font-size: 1.5em;
        user-select: none;
        color: white;
      }
    }
  }
  