@import '../variables.scss';

.toggle {
    &_label {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 30px;
    }
    
    &_input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    
    &_span {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 156px;
        right: -156px;
        bottom: 0;
        background: #FB5936;
        transition: 0.3s;
        border-radius: 30px;
        
        &::before {
            position: absolute;
            content: "";
            height: 25px;
            width: 25px;
            left: 3px;
            bottom: 2.6px;
            background-color: #fff;
            border-radius: 50%;
            transition: 0.3s;
        }
    }
      
    &_input:checked + &_span {
        background-color: $green;
        
        &:before {
            transform: translateX(29px);
        }
    }
}